.videos {
  display: flex;
  padding: 40px;
  flex-wrap: wrap;
  width: 1272px;
  padding-top: 10px;
  gap: 25px;
  > li {
    padding: 15px;
    padding-bottom: 30px;
    border-radius: 10px;
    &:hover {
      background-color: #3b3b3b;
      > div > div {
        > h3,
        > p {
          color: #fff;
          z-index: 2;
        }
      }
    }
  }
}

#root {
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
}

.videoImg {
  width: 242px;
  > img {
    border-radius: 10px;
  }
}

.videoText {
  margin-top: 10px;
  display: flex;
  align-items: flex-start;

  > div {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
    > img {
      border-radius: 50%;
    }
  }
  > div:nth-child(2) {
    width: 100%;
    > h3 {
      font-family: "Roboto";
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      margin-bottom: 7px;
      color: #ffffff;
    }
    > p {
      font-family: "Roboto";
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #aaaaaa;
    }
  }
}

.player {
  > div {
    display: flex;
  }
}

.box {
  display: flex !important;
}

iframe {
  margin-top: 20px;
  margin-left: 40px;
  display: inline-block;
}



main > section {
  display: flex;
  > ul {
    > li > a {
      gap: 5px;
      padding: 5px;

      display: flex;
      > div:nth-child(2) {
        > div:nth-child(1) {
          display: none;
        }
        > div > h3 {
          width: 150px;
        }
      }
    }
  }
}


main>div{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  width: 100vw;
  background-color: brown;
  width: 0px;
  animation-iteration-count: 100;
  animation-name: download;
  animation-duration: 1s;
}

@keyframes download{
  0%{
    width: 0;
  }
  100%{
    width: 100vw;
  }
}
