.search {
  padding: 10px 14px;
  width: 478px;
  background: #0f0f0f;
  border: 1px solid #323232;
  outline: none;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  border-radius: 30px 0px 0px 30px;
  line-height: 16px;
  color: #aaaaaa;
  border: 1px solid #323232;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  display: inline-block;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: #474747;
  border-radius: 5px;
}

.search_button {
  background: #323232;
  border-radius: 0px 30px 30px 0px;
  padding: 9px 19px;
  border: none;
  cursor: pointer;
  &::after {
    content: "";
    background-repeat: no-repeat;
    width: 17px;
    height: 17px;
    display: inline-block;
    background-image: url("./img/search.svg");
  }
}

.header {
  margin-top: 7px;
  padding-bottom: 7px;
}
body {
  background: #0f0f0f;
}

.search_form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 250px;
}

.microphone {
  background-color: #0f0f0f;
  padding: 15px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-image: url("./img/microphone.svg");
  background-color: transparent;
  border: none;
  cursor: pointer;
  background-position: center;
  width: 15px;
  margin-left: 14px;
  height: 22px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #323232;
  }
}

.header {
  display: flex;
  align-items: center;
}

.addVideo {
  padding: 20px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-image: url("./img/addVideo.svg");
  background-color: transparent;
  border: none;
  cursor: pointer;
  background-position: center;
  max-width: 20px;
  max-height: 28px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #323232;
  }
}
.table {
  padding: 20px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-image: url("./img/more.svg");
  background-color: transparent;
  border: none;
  cursor: pointer;
  background-position: center;
  max-width: 20px;
  max-height: 25px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #323232;
  }
}
.notification {
  padding: 20px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-image: url("./img/notifacation.svg");
  background-color: transparent;
  border: none;
  cursor: pointer;
  background-position: center;
  max-width: 20px;
  max-height: 25px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #323232;
  }
}

.account {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: 32px;
}

.profile {
  border-radius: 100px;
  padding: 10px 25px;
  background-color: transparent;
  display: flex;
  align-items: center;
  margin-left: 8px;
  cursor: pointer;
  border: 1px solid #3da6ff;
  color: #3da6ff;
  &:hover {
    color: seagreen;
    border-color: seagreen;
  }
}

.headerTop {
  border-bottom: 1px solid #383838;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  width: 100%;
  align-items: center;
  padding-bottom: 7px;
  justify-content: space-between;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.mainTop {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2px;
  margin-right: 10px;
  width: 100%;
}

.videoButtons {
  padding: 8px 12px;
  background: #383838;
  border: 1px solid #474747;
  border-radius: 25px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  cursor: pointer;
}

.mainTop {
  width: 100%;
  gap: 12px;
}

.hambarger {
  width: 29px;
  height: 29px;
  background-repeat: no-repeat;
  background-image: url("./img/hambarger.svg");
  border: none;
  margin-right: 18px;
  background-color: transparent;
  cursor: pointer;
  background-position: center;
  padding: 10px;
  border-radius: 25px;
  &:hover {
    border: 1px solid #474747;
  }
}

.defaultMenu {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  > button {
    border: none;
    background-color: transparent;
    font-family: "Roboto";
    padding: 10px;
    border-radius: 7px;
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;
    width: 90px;
    &:hover {
      background-color: #1c1c1c;
    }
    cursor: pointer;
    &::before {
      content: "";
      background-repeat: no-repeat;
      width: 100%;
      height: 24px;
      background-image: url("./img/homeIcon.svg");
      display: flex;
      align-items: center;
      justify-content: center;
      background-position: center;
      margin-bottom: 10px;
    }
  }

  > button:nth-child(2)::before {
    background-image: url("./img/shortIcon.svg");
  }
  > button:nth-child(3)::before {
    background-image: url("./img/notifacation.svg");
  }
  > button:nth-child(4)::before {
    background-image: url("./img/subIcon.svg");
  }
}

.menu {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.267);
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  > div {
    position: relative;
  }
}

.menuItem {
  width: 220px;
  background-color: #212121;
  height: 100vh;
  position: absolute;
  padding: 24px 10px;
  left: 0;
  bottom: 0;
  top: 0;
  overflow-y: scroll;
}

.menuHeader {
  display: flex;
  align-items: center;
  margin-bottom: 26px;
}

.menuPages:nth-child(2) {
  margin-top: 16px;
}
.menuBtn2 {
  border: none;
  background-color: transparent;
  font-family: "Roboto";
  padding: 10px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  margin-left: -20px;
  padding-left: 30px;
  cursor: pointer;
  &::before {
    content: "";
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    margin-right: 20px;
    background-image: url("./img/libraryIcon.svg");
    display: inline-block;
    vertical-align: middle;
  }
  width: 108%;
  &:hover {
    background: #383838;
  }
}

.menuMore {
  border: none;
  background-color: transparent;
  font-family: "Roboto";
  padding: 10px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  margin-left: -20px;
  padding-left: 30px;
  cursor: pointer;
  &::before {
    content: "";
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    margin-right: 20px;
    background-image: url("./img/moreIcon.svg");
    display: inline-block;
    background-position: center;
    vertical-align: middle;
  }
  width: 108%;
  &:hover {
    background: #383838;
  }
}
.menuBtn2:nth-child(2) {
  &::before {
    background-image: url("./img/historyIcon.svg");
  }
}

.menuBtn2:nth-child(3) {
  &::before {
    background-image: url("./img/youVideosIcon.svg");
  }
}

.menuBtn2:nth-child(4) {
  &::before {
    background-image: url("./img/watchLaterIcon.svg");
  }
}

.menuBtn2:nth-child(5) {
  &::before {
    background-image: url("./img/likedIcon.svg");
  }
}

.menuBtn {
  border: none;
  background-color: transparent;
  font-family: "Roboto";
  padding: 10px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  margin-left: -20px;
  padding-left: 30px;
  cursor: pointer;
  &::before {
    content: "";
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    margin-right: 20px;
    background-image: url("./img/homeIcon.svg");
    display: inline-block;
    vertical-align: middle;
  }
  width: 108%;
  &:hover {
    background: #383838;
  }
}

.menuBtn:nth-child(2) {
  &::before {
    background-image: url("./img/exploesIcon.svg");
  }
}

.menuBtn:nth-child(3) {
  &::before {
    background-image: url("./img/shortIcon.svg");
  }
}

.menuBtn:nth-child(4) {
  &::before {
    background-image: url("./img/subIcon.svg");
  }
}

.menuPages {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 26px;
  border-bottom: 1px solid #383838;
}

.menuSubItem {
  padding: 10px;
  display: flex;
  align-items: center;
  > div {
    > img {
      border-radius: 50%;
    }
  }
  > h3 {
    font-family: "Roboto";
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;
    margin-left: 21px;
    margin-right: 31px;
  }
  > p {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #3da6ff;
    margin-left: auto;
  }
}

.menuSubTitle {
  font-family: "Roboto";
  margin-top: 16px;
  font-weight: 600;
  font-size: 13px;
  line-height: 15px;
  color: #aaaaaa;
  padding: 10px;
}

.menu {
  display: none;
}

.search::after {
  content: "";
  background-repeat: no-repeat;
  width: 57px;
  height: 57px;
  display: inline-block;
  background-image: url("./img/search.svg");
  background-color: #bb0101;
}

.menuClose {
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  height: 100vh;
  width: 100vw;
}

.wrapper {
  display: flex;
  align-items: flex-start;
}
